import React, { useState, useEffect } from "react";
import { Link, Navigate, useParams } from "react-router-dom";
import { resetCartAsync } from "../features/cart/cartSlice";
import { useDispatch, useSelector } from "react-redux";
import { selectLoggedInUser } from "../features/auth/authSlice";
import { resetOrder, selectOrders } from "../features/order/orderSlice";
import OrderSummary from "../features/orderSummery/OrderSummery";
import axios from "axios";

function OrderSuccessPage() {
  const params = useParams();
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);

  const [loading, setLoading] = useState(true);
  const [showOrder, setShowOrder] = useState(false);
  const [userDetail, setUserDetail] = useState({});

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`/users/own`);
        setUserDetail(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching user:", error);
        setLoading(false);
      }
    };

    const timeout = setTimeout(() => {
      fetchData();
    }, 2500);

    return () => clearTimeout(timeout);
  }, [user.id]);

  useEffect(() => {
    dispatch(resetCartAsync(user.id));
    dispatch(resetOrder());
  }, [dispatch, user]);

  return (
    <>
      {!params.id && <Navigate to="/" replace={true}></Navigate>}
      {loading ? (
        <div className="relative flex justify-center items-center mt-24">
          <div className="absolute animate-spin rounded-full h-32 w-32 border-t-4 border-b-4 border-purple-500"></div>
          <img
            src="https://www.svgrepo.com/show/509001/avatar-thinking-9.svg"
            className="rounded-full h-28 w-28"
            alt="Loading..."
          />
        </div>
      ) : (
        <>
          <div className="flex justify-center mt-6 px-4 sm:px-6 lg:px-8">
            <div className="w-full max-w-4xl p-5 mx-auto">
              <p className="font-bold text-2xl sm:text-3xl md:text-4xl">Thank you for your purchase!</p>
              <p className="mt-5 text-sm sm:text-base">
                Your order will be processed within 24 hours during working days. We will notify you by email once your order has been shipped.
              </p>
              <img
                className="h-28 w-auto rounded-full mx-auto mt-5"
                alt="Your Company"
              />
              <p className="font-bold text-left mt-5">Billing address</p>

              <div>
                <table className="text-left mt-4 w-full">
                  <tbody>
                    <tr className="py-4">
                      <td className="w-32 font-bold">Name</td>
                      <td className="text-left">
                        {userDetail.addresses && userDetail.addresses.length > 0 && (
                          userDetail.addresses[0]?.name
                        )}
                      </td>
                    </tr>
                    <tr className="py-4">
                    <td className="font-bold">Address</td>
                    <td className="text-left">
                      {userDetail.addresses && userDetail.addresses.length > 0 && (
                        `${userDetail.addresses[0]?.street}, ${userDetail.addresses[0]?.city}, ${userDetail.addresses[0]?.state}, ${userDetail.addresses[0]?.pinCode}`
                      )}
                    </td>
                  </tr>
                  
                    <tr className="py-4">
                      <td className="font-bold">Phone</td>
                      <td className="text-left">
                        {userDetail.addresses && userDetail.addresses.length > 0 && (
                          userDetail.addresses[0]?.phone
                        )}
                      </td>
                    </tr>
                    <tr className="py-4">
                      <td className="font-bold">Email</td>
                      <td className="text-left">{userDetail.email}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div className="flex flex-col items-center mt-6 px-4 sm:px-6 lg:px-8">
            <Link to="/" className="mb-3">
              <button className="bg-gray-800 hover:bg-gray-700 text-white p-2 rounded-lg w-36 mb-3">
                Go back home
              </button>
            </Link>
            <p className="text-center">To get order summary, click the button below</p>
            <button
              onClick={() => setShowOrder(true)}
              className="bg-green-600 hover:bg-green-500 text-white p-2 rounded-lg w-56 mt-3"
            >
              Order Summary
            </button>
            {showOrder && <OrderSummary onClose={() => setShowOrder(false)} />}
          </div>
        </>
      )}
    </>
  );
}

export default OrderSuccessPage;

